<template>
  <router-link :to="{ name: 'home.index' }" :class="logoClass">
    <img :src="isDark ? darkLogo : lightLogo" class="top-nav-logo" />
  </router-link>
</template>

<script>
import LogoHeaderBlack from "@/assets/icons/logo-header.svg";
import LogoHeaderColorful from "@/assets/icons/logo-white.svg";

export default {
  name: "TopNavLogo",
  data: () => ({
    darkLogo: LogoHeaderColorful,
    lightLogo: LogoHeaderBlack,
  }),
  props: {
    isDark: {
      default: false,
      type: Boolean,
    },
    logoClass: {
      default: "",
      type: String,
      required: false,
    },
  },
};
</script>
<style>
.top-nav-logo {
  margin: 0 !important;
  /* width: 77px; */
}
</style>



