<script>
import { translateI18n } from "@/utils";
import {
  navMenusGroupA,
  navMenusGroupB,
  navMenusGroupC,
} from "./js/nav-menu.js";
import { getDownloadAppsPath, MacBrowserApp } from "@/bizlib/apps";

export default {
  name: "FootContainer",
  data() {
    return {
      lang: "",
      options: [
        { id: "zh-CN", text: "" },
        { id: "en", text: "English" },
        // {id:"zh-TW",text:""},
      ],
      copyright: "Copyright  2022 All rights reserved.",
    };
  },
  mounted() {
    // this.lang =this.$i18n.locale;
  },
  methods: {
    langChanged(lg) {
      // console.log(lg)
      const i18nLang = this.$i18n.locale;
      if (i18nLang !== lg) {
        this.$i18n.locale = lg;
        this.$store.commit("setLang", lg);
      }
    },
    gotoExtensionDownload() {
      this.$router.push({
        name: "extension.index",
        query: { id: 3 },
      });
    },
    gotoHelpCenter() {
      this.$router.push({
        name: "help.issue",
      });
    },
    gotoMailDetailPage() {
      this.$router.push({
        name: "appdetail.index",
        query: { id: 1 },
      });
    },
  },
  computed: {
    ...Vuex.mapState({
      currentLang: (state) => {
        this.lang = state.currentLang;
      },
    }),
    socialMenus() {
      return socialMenus;
    },
    navMenusA() {
      return navMenusGroupA;
    },
    navMenuB() {
      return navMenusGroupB;
    },
    navMenuC() {
      return navMenusGroupC;
    },
    getBrowserMacHref() {
      return getDownloadAppsPath(MacBrowserApp);
    },
  },
};
</script>
