
/**
 * xxx @126
 * only valid prefix
 *  ~!#$%^&*()/\|,<>?"'();:+=[]{}  
 * @param {*} prefix
 */
export function BMailAccountIllegal(prefix) {
  if (typeof prefix === 'undefined') return true;
  return /[~!#$%^&*()\/\\|,<>?"'();:+=\[\]{} ]/.test(prefix)
}

export default {
  BMailAccountIllegal
}



